<template>
	<div class="goods">
		<div class="mune-wrapper" v-el:menu-wrapper>
			<ul>
				<li class="mune-item border-1px" v-for="item in goods" :class="{'current':currentIndex === $index}" @click="selectMenu($index,$event)">
					<span class="text"><span v-show="item.type>0" class="icon" :class="classMaps[item.type]"></span>{{item.name}}</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper" v-el:foods-wrapper>
			<ul>
				<li v-for="item in goods" class="food-list food-list-hook">
					<h1 class="title">{{item.name}}</h1>
					<ul>
						<li v-for="food in item.foods" class="foods-item border-1px" @click="selectFood(food,$event)">
							<div class="icon">
								<img :src="food.icon">
							</div>
							<div class="content">
								<h2 class="name">{{food.name}}</h2>
								<p class="des">{{food.description}}</p>
								<div class="extra">
									<span class="count">月售{{food.sellCount}}</span><span>好评率{{food.rating}}%</span>
								</div>
								<div class="price">
									<span class="now">￥{{food.price}}</span><span v-show="food.oldPrice" class="old">{{food.oldPrice}}</span>
								</div>
								<div class="cartcontrol-wrapper">
									<cartcontrol :food="food"></cartcontrol>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart v-ref:shopcart :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" :select-foods="selectFoods"></shopcart>
		<food :food="selectedFood" v-ref:food></food>
	</div>
</template>
<script>
	import BScroll from 'better-scroll'
	import shopcart from 'components/shopcart/shopcart.vue'
	import cartcontrol from 'components/cartcontrol/cartcontrol.vue'
	import food from 'components/food/food.vue'

	const ERR_OK = 0

	export default {
		props: ['seller'],
		data () {
			return {
				goods: [],
				listHeight: [],
				scrollY: 0,
				selectedFood: {}
			}
		},
		computed: {
			currentIndex () {
				for (let i = 0; i < this.listHeight.length; i++) {
					let currentHeight1 = this.listHeight[i]
					let currentHeight2 = this.listHeight[i + 1]
					if (!currentHeight2 || (this.scrollY >= currentHeight1 && this.scrollY < currentHeight2)) {
						return i
					}
				}
				return 0
			},
			selectFoods () {
				let foods = []
				this.goods.forEach((good) => {
					good.foods.forEach((food) => {
						if (food.count) {
							foods.push(food)
						}
					})
				})
				return foods
			}
		},
		created () {
			this.classMaps = ['decrease', 'discount', 'guarantee', 'invoice', 'special']

			this.$http.get('goods.json').then((response) => {
				response = response.body
				if (response.errno === ERR_OK) {
	                this.goods = response.data
	                this.$nextTick(() => {
	                this._initScroll()
	                this._calculateHeight()
	                })
	            }
			})
		},
		methods: {
			_initScroll () {
				this.menuScroll = new BScroll(this.$els.menuWrapper, {
					click: true
				})

				this.foodsScroll = new BScroll(this.$els.foodsWrapper, {
					click: true,
					probeType: 3
				})

				this.foodsScroll.on('scroll', (pos) => {
					this.scrollY = Math.abs(Math.round(pos.y))
				})
			},
			_calculateHeight () {
				const foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook')
				let height = 0
				this.listHeight.push(height)
				for (let i = 0; i < foodList.length; i++) {
					const item = foodList[i]
					height += item.clientHeight
					this.listHeight.push(height)
				};
			},
			selectMenu (index, ev) {
				if (!ev._constructed) {
					return
				}
				let foodLst = this.$els.foodsWrapper.getElementsByClassName('food-list-hook')
				let el = foodLst[index]
				this.foodsScroll.scrollToElement(el, 300)
			},
			_drop(target) {
				// 优化体验，异步执行下落动画
				this.$nextTick(() => {
					this.$refs.shopcart.drop(target)
				})
			},
			selectFood(food, ev) {
				if (!ev._constructed) {
					return
				}
				this.selectedFood = food
				this.$refs.food.show()
			}
		},
		components: {
			shopcart,
			cartcontrol,
			food
		},
		events: {
			'cart.addCart'(target) {
				this._drop(target)
			}
		}
	}
</script>
<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixin.styl"

	.goods
		display: flex
		position: absolute
		top: 174px
		bottom: 46px
		width: 100%
		overflow: hidden
		.mune-wrapper
			flex: 0 0 80px
			width: 80px
			background: #f3f5f7
			.mune-item
				display: table
				height: 54px
				width: 56px
				line-height: 14px
				padding: 0 12px
				border-1px(rgba(7, 17, 27, 0.1))
				&.current
					position: relative
					margin-top: -1px
					font-weight: 700
					background: #fff
				.icon
					display: inline-block
					vertical-align: top
					width: 12px
					height: 12px
					margin-right: 2px
					background-size: 12px 12px
					background-repeat: no-repeat
					&.decrease
						bg-image('decrease_3')
					&.discount
						bg-image('discount_3')
					&.guarantee
						bg-image('guarantee_3')
					&.invoice
						bg-image('invoice_3')
					&.special
						bg-image('special_3')	
				.text
					display: table-cell
					font-size: 12px
					width: 56px
					vertical-align: middle
		.foods-wrapper
			flex: 1
			background: #fff
			.title
				padding-left: 14px
				height: 26px
				line-height: 26px
				border-left: 2px solid #d9dde1
				font-size: 12px
				color: rgb(147, 153, 159)
				background: #f3f5f7
			.foods-item
				display: flex
				margin: 18px
				padding-bottom: 18px
				border-1px(rgba(7, 17, 27, 0.1))
				&:last-child
					border-none()
					margin-bottom: 0
				.icon
					flex: 0 0 57px
					margin-right: 10px
					img
						width: 57px
				.content
					flex: 1
					.name
						margin: 2px 0 8px
						font-size: 14px
						height: 14px
						line-height: 14px
						color: rgb(7, 17, 27)
					.des,.extra
						line-height: 10px
						font-size: 10px
						color: rgb(147, 153, 159)
					.des
						margin-bottom: 8px
						line-height: 12px
					.extra
						.count
							margin-right: 12px
					.price
						font-weight: 700
						line-height: 24px
						.now
							margin-right: 8px
							font-size: 14px
							color: rgb(240, 20, 20)
						.old
							text-decoration: line-through
							font-size: 10px
							color: rgb(147, 153, 159)
					.cartcontrol-wrapper
						position: absolute
						right: 0
						bottom: 12px	
</style>


